<script>
import { UploadFilled } from '@element-plus/icons-vue';
import {ElMessage} from "element-plus";
export default {
  name: "AddDrivers",
  components: { UploadFilled },
  data(){
    return {
      addDriverForm: {
        photo: null,
        firstName: "",
        lastName: "",
        contactNumber: "",
        emp_id: "",
        gender: "",
        dob: "",
        address: "",
        city: "",
        state: "",
        fleet: "",
        description: "",
        licenseFront: null,
        licenseBack: null,
        document: "",
        expiryDate: "",
        pinCode: "",
        acFront: null,
        acBack: null
      }
    }
  },
  methods: {
    submitForm(){
      // console.log(this.addDriverForm.photo);
      this.$refs.driverForm.validate(
          (valid, fields) => {
            if(valid){
              let data = {
                first_name: this.addDriverForm.firstName,
                last_name: this.addDriverForm.lastName,
                phone: this.addDriverForm.contactNumber,
                emp_id: this.addDriverForm.emp_id,
                gender: this.addDriverForm.gender,
                dob: this.addDriverForm.dob,
                address: this.addDriverForm.address,
                city: this.addDriverForm.city,
                state: this.addDriverForm.state,
                pincode: this.addDriverForm.pinCode
              };

              // TODO: For normal post form data
              let str = "";
              Object.keys(data).forEach(each=>{
                str = str + (each + "=" + data[each] + "&");
              });
              str = str.substring(0, str.length - 1);

              // TODO: For multipart post form data
              let formData = new FormData();
              formData.append( "photo", this.addDriverForm.photo );
              formData.append( "license_front", this.addDriverForm.licenseFront );
              formData.append( "license_back", this.addDriverForm.licenseBack );
              formData.append( "aadhaar_card_front", this.addDriverForm.acFront );
              formData.append( "aadhaar_card_back", this.addDriverForm.acBack );

              let promiseArray = [];

              promiseArray.push(fetch("/addDriver", {
                method: "POST",
                headers: {'Content-Type': 'application/x-www-form-urlencoded'},
                // headers: {'Content-Type': 'multipart/form-data'},
                body: str
              }));

              promiseArray.push(fetch("/addDriverFiles", {
                method: "POST",
                // headers: {'Content-Type': 'application/x-www-form-urlencoded'},
                headers: {'Content-Type': 'multipart/form-data'},
                body: formData
              }));

              Promise.all(promiseArray).then(res => {
                ElMessage({
                  type: 'success',
                  message: 'Successfully added!',
                });
              });
            }
          }
      );
    }
  }
}
</script>

