<script>
import { ElMessage, ElMessageBox } from 'element-plus'
import { Plus, Edit, View, Search, CaretBottom } from '@element-plus/icons-vue'
import MenuOption from "@/admin/components/MenuOption";
export default {
  name: "ViewDrivers",
  components: {MenuOption, Search, CaretBottom },
  data(){
    return {
      searchPopover: false,
      tableData: [],
      multiSelectArray: [],
      showAdvanceFilters: false,
      searchColumns: [
        { name: "Name", value: "DRIVER_NAME"},
        { name: "Employee id", value: "DRIVER_ID"},
        { name: "Phone Number", value: "PH_NO"},
        { name: "Distance", value: "TOTAL_DISTANCE"},
        { name: "Duration", value: "TOTAL_MINUTES"},
        { name: "Cash", value: "TOTAL_CASH"},
        { name: "Digital", value: "TOTAL_DIGITAL"},
        { name: "Rides", value: "TOTAL_RIDES"}
      ],
      selectedSearch: "DRIVER_NAME",
      searchVal: "",
      p_perPage: 25,
      p_pageNo: 1,
      filterCols: {

      }
    }
  },
  computed: {
    filterTableData() {
      return this.tableData.filter(
          (data) =>
              !this.searchVal ||
              data[this.selectedSearch].toLowerCase().includes(this.searchVal.toLowerCase())
      )
    },
    p_total(){
      return this.tableData.length;
    },
    getPlusIcon(){
      return Plus;
    },
    getEditIcon(){
      return Edit;
    },
    getViewIcon(){
      return View;
    },
    // tableData(){
    //   return [
    //     {
    //       DRIVER_NAME: "Jaswanth SP",
    //       PH_NO: "6381360525",
    //       LICENSE: "TN012379",
    //       ONLINE: true,
    //       LAST_ONLINE_TIME: "2016-05-01 1:12 am",
    //       REGISTERED_DATE: "2016-05-02",
    //       STATUS: true,
    //       EMERGENCY_NO: "27918033"
    //     },
    //     {
    //       DRIVER_NAME: "Sivakumar",
    //       PH_NO: "6381360312",
    //       LICENSE: "TN012379",
    //       ONLINE: false,
    //       LAST_ONLINE_TIME: "2016-05-01 1:12 pm",
    //       REGISTERED_DATE: "2016-05-01",
    //       STATUS: false,
    //       EMERGENCY_NO: "198112131"
    //     }
    //   ]
    // }
  },
  methods: {
    eventHandler(menuClicked, rowData){
      console.log(menuClicked, rowData);
    },
    filteredMenus(rowData){
      if(rowData.STATUS) return [{ name: "Set Inactive", key: "set_inactive"}];
      else return [{ name: "Set Active", key: "set_active"}];
    },
    searchSelected(each){
      this.selectedSearch = each.value;
      this.searchPopover = false;
    },
    openRoute(){
      window.location.href = '#/drivers/addDriver';
    },
    getTableData(){
      const url = "/drivers";
      fetch(url).then(response => response.json())
          .then(res => this.tableData = res.DATA);
    },
    deleteFunc(){
      const url = "/deleteDriver";
      console.log(this.multiSelectArray[0]);
      if(this.multiSelectArray.length !== 0){
        ElMessageBox.confirm(
            'Are you sure want to delete?',
            'Warning',
            {
              confirmButtonText: 'OK',
              cancelButtonText: 'Cancel',
              type: 'warning',
            }
        ).then(() => {
          let arrLen = this.multiSelectArray.length;
          let promiseArr = [];
          for(let i = 0; i < arrLen; i++){
            promiseArr.push(fetch(url, { method: "POST",  headers: {'Content-Type': 'application/x-www-form-urlencoded'}, body: "id=" + this.multiSelectArray[i].DRIVER_ID }))
          }
          Promise.all(promiseArr).then(res =>{
                this.getTableData();
                ElMessage({
                  type: 'success',
                  message: 'Successfully deleted!',
                })
              });
              });

      }
      else{
          ElMessage({
            type: 'info',
            message: 'Please select rows to delete',
          })
      }
    },
    // exportFunc(){
    //   ElMessage({
    //     type: 'success',
    //     message: 'Exported as CSV',
    //   })
    // },
    handleSelectionChange(selected){
      if(typeof selected === undefined) return;
     this.multiSelectArray = selected;
    },
    filterHandler(value, row, column){
      const property = column['property'];
      return row[property] === value;
    },
    filterChange(filters){
      console.log(filters);
    }
  },
  mounted() {
    this.getTableData();
  }
}
</script>

