<script>
import { ElMessage, ElMessageBox } from 'element-plus'
import { Plus, Edit, View, Search, CaretBottom, Filter } from '@element-plus/icons-vue'
export default {
  name: "ViewDrivers",
  components: { Search, CaretBottom, Filter },
  data(){
    return {
        shortcuts: [
            {
              text: 'Last week',
            },
            {
              text: 'Last month',
            },
            {
              text: 'Last 3 months'
            }
        ],
      value2: "",
      stats: {
        cash: "",
        duration: "",
        distance: "",
        digital: ""
      },
      statusMap: {
        ONGOING: { displayName: "Ongoing", color: "#f9c700" },
        COMPLETED: { displayName: "Completed", color: "#016a4e" },
      },
      multiSelectArray: [],
      showAdvanceFilters: false,
      searchColumns: [
        { name: "Driver phone no", value: "DRIVER_NAME"},
        { name: "Vehicle No", value: "VEHICLE"},
        { name: "Distance", value: "DISTANCE"},
        { name: "Duration", value: "DURATION"},
        { name: "Cash", value: "CASH"},
        { name: "Digital", value: "DIGITAL"}
      ],
      tableData: [],
      selectedSearch: "DRIVER_name",
      searchVal: "",
      p_perPage: 25,
      p_pageNo: 1,
      filterCols: {

      }
    }
  },
  computed: {
    filterTableData() {
      return this.tableData.filter(
          (data) => {
            if(this.selectedSearch.includes(".")){
              let returnBool = false;
              let arr = this.selectedSearch.split(".");
              let obj = {};
              arr.forEach((each, index)=>{
                if(index === arr.length - 1){
                  if(obj.hasOwnProperty(each)){
                    returnBool = obj[each].toLowerCase().includes(this.searchVal.toLowerCase())
                  }
                }
                else if(index === 0){
                  obj = {...data[each]};
                }
                else{
                  obj = {...obj[each]};
                }
              });
              return returnBool;
            }
            return !this.searchVal || data[this.selectedSearch].toLowerCase().includes(this.searchVal.toLowerCase())
          }
      )
    },
    p_total(){
      return this.tableData.length;
    },
    getPlusIcon(){
      return Plus;
    },
    getEditIcon(){
      return Edit;
    },
    getViewIcon(){
      return View;
    },
    // tableData(){
    //   return [];
    // }
  },
  methods: {
    getRideStats(){
      const url = "/stats/rides";
      fetch(url).then(response => response.json())
          .then(res =>{
            let obj = res.DATA[0];
            this.stats.distance = obj.TOTAL_DISTANCE.toFixed(2);
            this.stats.duration = obj.TOTAL_DURATION;
            this.stats.cash = obj.TOTAL_CASH.toFixed(2);
            this.stats.digital = obj.TOTAL_DIGITAL.toFixed(2);
          });
    },
    getUTC(time){
      let dateIST = new Date(time);
      dateIST.setHours(dateIST.getHours() + 5);
      dateIST.setMinutes(dateIST.getMinutes() + 30);
      return dateIST;
    },
    getTableData(){
      const url = "/rides";
      fetch(url).then(response => response.json())
          .then(res => this.tableData = res.DATA);
    },
    deleteFunc(){
      if(this.multiSelectArray.length !== 0){
        ElMessageBox.confirm(
            'Are you sure want to delete?',
            'Warning',
            {
              confirmButtonText: 'OK',
              cancelButtonText: 'Cancel',
              type: 'warning',
            }
        ).then(() => {
          ElMessage({
            type: 'success',
            message: 'Delete completed',
          })
        });
      }
      else{
        ElMessage({
          type: 'info',
          message: 'Please select rows to delete',
        })
      }
    },
    exportFunc(){
      ElMessage({
        type: 'success',
        message: 'Exported as CSV',
      })
    },
    handleSelectionChange(selected){
      if(typeof selected === undefined) return;
      this.multiSelectArray = selected;
    },
    filterHandler(value, row, column){
      const property = column['property'];
      return row[property] === value;
    },
    filterChange(filters){
      console.log(filters);
    }
  },
  mounted() {
    this.getRideStats()
    this.getTableData();
  }
}
</script>

